<template>
	<core-page>
		<div class="omapi-personalization">
			<div class="omapi-personalization__filters">
				<div class="omapi-personalization__nav flex">
					<a
						v-for="category in categories"
						:key="category.slug"
						class="omapi-personalization__filter nohover"
						:class="{ 'omapi-personalization__filter-active': !search && filter === category.slug }"
						@click="updateFilter(category.slug)"
					>
						{{ category.displayName }}
					</a>
					<a
						class="omapi-personalization__filter nohover"
						:class="{ 'omapi-personalization__filter-active': allFilters }"
						@click="filter = ''"
						>All</a
					>
				</div>
				<div class="omapi-personalization__search campaign-type-filter__search">
					<input
						placeholder="Search..."
						type="text"
						class="omapi-input"
						:value="search"
						@input="updateSearch"
						@keydown.esc="clearSearch"
					/>
					<svg-times v-if="search" class="clear-search" @click="clearSearch" />
				</div>
			</div>

			<common-alerts id="om-plugin-alerts" :alerts="alerts" />

			<core-loading v-if="isLoading"><h1>Loading...</h1></core-loading>
			<personalization-rules
				v-else
				:categories="categories"
				:filter="filter"
				:search="search"
				@clearSearch="clearSearch"
			/>
		</div>
	</core-page>
</template>

<script>
import { mapActions } from 'vuex';

export default {
	data() {
		return {
			filter: 'popular',
			search: '',
			categories: [
				{
					slug: 'popular',
					displayName: 'Popular',
					order: 1,
				},
				{
					slug: 'timing',
					displayName: 'Trigger (When)',
					order: 2,
				},
				{
					slug: 'triggers',
					displayName: 'Targeting (Who)',
					order: 3,
				},
				{
					slug: 'behavior',
					displayName: 'Personalization',
					order: 4,
				},
				{
					slug: 'ecommerce',
					displayName: 'Ecommerce',
					order: 5,
				},
			],
		};
	},
	computed: {
		isLoading() {
			return !this.$store.getters.isFetched('rulesetData');
		},
		alerts() {
			const alerts = [...this.$get('$store.state.alerts', [])];
			return alerts.concat(this.$get('$store.state.campaigns.alerts', []));
		},
		allFilters() {
			return this.search || !this.filter;
		},
	},
	mounted() {
		if (this.$store.getters.shouldFetchUser) {
			this.$bus.$on('fetchedMe', this.fetchRulesetData);
			this.fetchMe();
		} else {
			this.fetchRulesetData();
		}
	},
	beforeDestroy() {
		this.$bus.$off('fetchedMe', this.fetchRulesetData);
	},
	methods: {
		...mapActions(['fetchMe']),
		...mapActions('campaigns', ['fetchRulesetData']),
		updateFilter(filter) {
			this.filter = filter;
			this.search = '';
		},
		clearSearch() {
			this.search = '';
		},
		updateSearch(e) {
			this.search = e.target.value;
		},
	},
};
</script>

